@import url(https://fonts.googleapis.com/css?family=Lobster|Righteous|Staatliches&display=swap);
/* RESET h* */
h1,h2,h3,h4,h5,h6,p {
    margin: 0;
}

a {
    text-decoration: none;
}
#header {
    display: flex;
    width: 100vw;
    height: 10vh;
    background-color: #05010e;
    color: white;
    font-family: 'Staatliches', cursive;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-around;
}

#menu {
    width: 40vw;
}

#title {
    width: 50vw;
}

#title {
    margin-left: 5vw;
}

#menu {
    font-family: 'Righteous', cursive;
}

.menu_item {
    display: inline-block;
    width: 33%;
    text-align: center;
}

.menu_item a {
    color: white;
}

@media (min-width: 1280px) {
    #title {
        font-size: 200%;
    }

    #menu {
        font-size: 130%;
    }
}
#home {
    display: flex;
    height: 90vh;
    width: 100vw;
    background: #042738 url(/static/media/background_home.c0f33811.jpg) no-repeat;
    background-origin: border-box;
    background-size: 100% 100%;
}

#about {
    padding-top: 5vw;
    padding-left: 5vw;
}

#social_media {
    position: absolute;
    left: 10vw;
    bottom: 10vh;
}
#about_me p {
    font-family: 'Lobster', cursive;
    color: #ffffff;
    text-align: center;
    font-size: 150%;
}
#social_networks, .sn a {
    color: white;
    font-family: 'Righteous', cursive;
}

.sn {
    display: inline-block;
    width: 6vw;
    text-align: center;
}


